.accordion {
    max-width: 600px;
    margin: 2rem auto;
  }
  
  .accordion-title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    cursor: pointer;
    background-color: #21aeca;
  }
  
  .disclaimer {
    color: red;
    padding-bottom: 20px;
  }

  .accordion-title:hover {
    background-color: #3ab4cc;
  }
  
  .accordion-title,
  .accordion-content {
    padding: 1rem;
  }
  
  .accordion-content {
    background-color: #39b9d2;
  }

  .accordion-content li {
    list-style-type: none;
  }
 
  @media screen and (max-width: 700px) {
    .accordion {
      width: 90%;
    }
  }